import styles from './style.module.css';
import { Link } from 'react-router-dom';
import Picture_1 from '../../data/header_footer/Picture_1.png'
import logoImage from '../../data/header_footer/Logo.png'

const Footer = () => {
  return (
    <div className={styles.container}>
      <nav className={styles.navbar}>
        <Link to='/' className={styles.links}> </Link>
        <Link to='/about' className={styles.links}> </Link>
        <Link to='/info' className={styles.links}> </Link>
        <Link to='/experimental-task' className={styles.links}> 17</Link>
        <Link to='/resources' className={styles.links}>  </Link>
      </nav>
      <div className={styles.line}></div>
      <img src={Picture_1} className={styles.picture} alt=''></img>
      <div className={styles.text_logo}>
        <p className={styles.text}>. . .</p>
        <Link to='/'><img src={logoImage} alt="" className={styles.logo} /></Link>
      </div>
    </div>
  );
};

export default Footer;
